@charset "UTF-8";

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 960px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

header {
  background-color: #eb4f4e;
}

header .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
  align-items: center;
}

header .items .logo {
  margin: 10px;
  color: #f0f0f0;
}

header .items .logo .title {
  vertical-align: bottom;
  margin-left: 10px;
  display: inline-block;
  font-size: 25px;
}

header .items .logo .text {
  margin-top: 5px;
}

header .items .menu {
  list-style: none;
}

header .items .menu ul {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

header .items .menu .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  border-radius: 5px;
  padding: 10px;
}

header .items .menu .item:hover {
  background-color: #232324;
}

header .items .menu .item:active {
  background-color: #232324;
}

header .items .menu .item a {
  font-weight: bold;
  color: #f0f0f0;
  text-decoration: none;
}

/* .debug {
    @include debug(rgba(0, 0, 0, 0.5), 1px solid #ff0);
} */

h2,
.h2 {
  font-size: 2.5rem;
  text-align: center;
  margin: 20px 0;
}

.h2sub {
  text-align: center;
  font-size: 20px;
  margin: 20px 0;
}

.profit {
  background: #232324;
  padding: 15px 0;
}

.profit h2 {
  color: #fff;
}

.profit .h2sub {
  color: #6cc372;
}

.profit .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: center;
}

.profit .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  /*------------------------------------*/
  width: calc(33.33333% - 10px);
  /*Делаем адаптив под планшеты. По умолчанию md.*/
  /*Задаем любые свойства и значения для sm экрана */
  /*Для блока параметров нескольких параметров */
  /*сколько breakPoints столько и можно сделать адптива size-...да хоть под 
            телевизор, если попросит заказчик */
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
}

.profit .items .item .img {
  border-radius: 50%;
}

.profit .items .item .subtitle {
  color: #fff;
  font-size: 1.5rem;
  margin: 3px 0;
}

.profit .items .item .text {
  font-size: 16px;
  color: #d94948;
}

.humans {
  background: #6cc372;
  padding: 10px 0;
}

.humans h2 {
  color: #232324;
}

.humans .h2sub {
  color: #fff;
}

.humans .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

.humans .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(25% - 10px);
  border-radius: 10px;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 10px;
  text-align: center;
  background: #fff;
  /*для первой картинки приходится использовать flex вручную - тут smartgrid не поможет...*/
  display: flex;
  flex-direction: column;
}

.humans .items .item img {
  margin: 10px auto;
  width: 90%;
}

.humans .items .item .subtitle {
  color: #6cc372;
  font-size: 24px;
  margin: 10px;
}

.humans .items .item .job {
  /*прописываем отступы чтобы выровнить кнопку у первой картинки */
  margin: auto 10px 10px 10px;
}

.humans .items .item .follow {
  background: #6cc372;
  color: #fff;
  font-size: 24px;
  cursor: pointer;
  border-radius: 5px;
  padding: 5px;
}

.portfolio {
  background: #f0f0f0;
  /*5 Дз- у меня было то же самое, но с margin у item */
  padding: 10px 0 200px;
}

.portfolio h2 {
  color: #232324;
}

.portfolio .h2sub {
  color: #6cc372;
}

.portfolio .items {
  margin-left: -5px;
  margin-right: -5px;
}

.portfolio .items::after {
  display: block;
  content: "";
  clear: both;
}

.portfolio .items .item {
  float: left;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(25% - 10px);
  margin-top: 15px;
  text-align: center;
}

.portfolio .items .item img {
  margin: 10px auto;
  width: 100%;
}

.portfolio .items .item .txt {
  color: #232324;
  font-size: 20px;
}

.pricing .h2sub {
  color: #eb4f4e;
}

.pricing .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  align-items: flex-end;
  justify-content: center;
  /*5 Дз - я прописал в item... */
  margin-top: -145px;
}

.pricing .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(33.33333% - 10px);
  text-align: center;
  border-radius: 10px;
  overflow: hidden;
}

.pricing .items .item .title {
  background: #fff;
  overflow: hidden;
}

.pricing .items .item ul li {
  background: #f0f0f0;
  padding: 15px 0;
  border-bottom: 1px solid #232324;
}

.pricing .items .item .price {
  background: #eb4f4e;
  color: #fff;
  padding: 10px 15px;
}

.pricing .items .item .price .cur {
  font-size: 1.5em;
}

.pricing .items .item .price .val {
  font-size: 3em;
}

.pricing .items .item .price .per {
  font-size: 1.2em;
  margin-top: 10px;
}

.pricing .items .item .price a {
  color: #232324;
  text-decoration: none;
  background: #fff;
  padding: 5px 20px;
  border-radius: 5px;
  margin-left: auto;
}

.test {
  margin: 20px 0;
}

.test .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

.test .items .item {
  /*стратегия col-padding для сливания цветов блоков */
  word-wrap: break-word;
  padding-left: 5px;
  padding-right: 5px;
  min-height: 100px;
}

.test .items .item-left {
  background-color: #ccc;
  width: calc(33.33333% - 10px);
  padding-right: 10px;
  padding-left: 0;
  margin-left: 5px;
}

.test .items .item-right {
  background-color: #f90;
  width: calc(66.66667% - 10px);
  padding-left: 0;
  padding-right: 0;
  margin-right: 5px;
}

/*Пример сетки вложенной в другую сетку */

footer .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

footer .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(33.33333% - 10px);
  background: #aaa;
  min-height: 200px;
  /*прослойка для вложенной сетки( или flex-box)*/
}

footer .items .item .row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

footer .items .item .row div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% - 10px);
  background: #f50;
  margin-bottom: 5px;
  padding: 5px;
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .profit .items {
    justify-content: center;
  }

  .profit .items .item {
    width: calc(41.66667% - 10px);
  }

  .humans .items .item {
    width: calc(50% - 10px);
  }

  .portfolio .items .item {
    width: calc(50% - 10px);
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .profit .items .item {
    width: calc(50% - 10px);
  }

  .profit .items .item {
    background-color: red;
    padding: 50px;
  }

  .portfolio {
    padding: 10px 0;
  }

  .pricing .items {
    margin-top: 10px;
  }

  .pricing .items .item {
    width: calc(66.66667% - 10px);
  }

  footer .items .item {
    width: calc(100% - 10px);
  }

  footer .items .item .row div {
    width: calc(33.33333% - 10px);
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .profit .items .item {
    width: calc(100% - 10px);
  }

  .humans .items {
    justify-content: center;
  }

  .humans .items .item {
    width: calc(83.33333% - 10px);
  }

  .portfolio .items .item {
    width: calc(83.33333% - 10px);
  }

  .portfolio .items .item {
    margin-left: calc(8.33333% + 5px);
  }

  .portfolio .items .item {
    margin-right: calc(8.33333% + 5px);
  }

  .pricing .items .item {
    width: calc(83.33333% - 10px);
  }

  footer .items .item .row div {
    width: calc(100% - 10px);
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .portfolio .items .item {
    width: calc(100% - 10px);
  }

  .portfolio .items .item {
    margin-left: calc(0% + 5px);
  }

  .portfolio .items .item {
    margin-right: calc(0% + 5px);
  }
}